.column-flex {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  margin-top: 100px;
  margin-bottom: 50px;
}

.description-text {
  color: #999999;
}

.App-link {
  color: #61dafb;
}

.search-bar-container {
  width: 500px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.search-bar {
  flex: 1;
}

.keyboard {
  width: 600px;
  margin-top: 50px;
}

.text-to-analyze {
  border: 1px solid #555566;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 50px;
}

.invalid-input-error {
  color: #ff0000;
  font-weight: bold;
  margin-bottom: 50px;
}

.canvas {
  margin-bottom: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
