
.keyboard-button {
  margin: 2px 0px;
  height: 30px;
  border-radius: 5px;
  border: 1px solid #4a5963;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.1s ease-out, border-color 0.1s ease-out;
  user-select: none;
  cursor: pointer;
}

.keyboard-button:hover {
  background-color: #44505a;
  border-color: #777777;
}
